<template>
  <echart :option="option" />
</template>
<script>
import Echart from "@/components/Echart.vue";

export default {
  mounted() {
    this.$unlock();
  },
  data() {
    return {
      option: {
        title: {
          text: "旅游项目",
          // subtext: "纯属虚构",
          left: "center",
          textStyle: {
            //文字的样式
            color: "#ccc",
            fontSize: 16,
          },
        },
        backgroundColor: "rgba(0,0,0,0)",
        tooltip: {
          trigger: "item",
          formatter: "{b}  <br/>{c}",
        },
        legend: {
          // 图例
          left: "center",
          top: "bottom",
          x: "center",
          y: "5%",
          //  orient: 'vertical', 垂直
          data: ["已规划", "筹备中", "建设中", "已完成"],
          icon: "circle",
          textStyle: {
            color: "rgba(136, 140, 173, 1)",
          },
        },
        calculable: true,
        series: [
          {
            name: "车型",
            type: "pie",
            //起始角度，支持范围[0, 360]
            startAngle: 0,
            //饼图的半径，数组的第一项是内半径，第二项是外半径
            radius: [25, 70],
            //支持设置成百分比，设置成百分比时第一项是相对于容器宽度，第二项是相对于容器高度
            center: ["50%", "25%"],
            color: ["#00A1E4", "#FF7E00","#24C768","#FFD900"],
            //是否展示成南丁格尔图，通过半径区分数据大小。可选择两种模式：
            // 'radius' 面积展现数据的百分比，半径展现数据的大小。
            //  'area' 所有扇区面积相同，仅通过半径展现数据大小
            roseType: "area",
            //是否启用防止标签重叠策略，默认开启，圆环图这个例子中需要强制所有标签放在中心位置，可以将该值设为 false。
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: true,
                formatter: "{c}%",
              },
              emphasis: {
                show: true,
              },
            },
            labelLine: {
              normal: {
                show: true,
                length2: 1,
